<template>
  <div class="home">
      {{film.title}}
      <iframe :src="film.iframe_src" frameborder="0" allowfullscreen></iframe>
      id_url: {{$route.params.id}}
  </div>
</template>

<script>
import {mapActions, mapGetters} from 'vuex'

export default {
  name: 'Home',
  computed: {
    ...mapGetters(['film'])
  },
  methods: {
    ...mapActions(['getFilm'])
  },
  mounted() {
    this.getFilm(this.$route.params.id)
  },
}
</script>
